if(local){
  store.commit('CHECK_USER',JSON.parse(local))
}

import {
  GoodsAction,
  GoodsActionIcon,
  GoodsActionButton,
  Card,
  SubmitBar,
  Swipe, SwipeItem
} from 'vant';

Vue.use(GoodsAction,).use(GoodsActionIcon).use(GoodsActionButton).use(Card).use(SubmitBar).use(Swipe).use(SwipeItem);


new Vue({
  render: h => h(App),
  router,
  store
}).$mount('#app')

